import React from 'react';
import { Switch, Route, Link } from 'react-router-dom';

import Login from './pages/Login/Login';

import './App.css';

export default function Router() {
    return (
        <div>
            <header>
                <nav>
                    <ul className="nav-area">
                        <li>
                            <Link to="/">Home</Link>
                        </li>
                        <li>
                            <Link to="/patients">Patients</Link>
                        </li>
                        <li>
                            <Link to="/reports">Reports</Link>
                        </li>
                        <li>
                            <Link to="/settings">Settings</Link>
                        </li>
                    </ul>
                </nav>
            </header>

            <Switch>
                <Route path="/patients">
                    <About />
                </Route>
                <Route path="/reports">
                    <Users />
                </Route>
                <Route path="/settings">
                    <Users />
                </Route>
                <Route path="/">
                    <Login />
                </Route>
            </Switch>
        </div>
    );
}

function Home() {
    return <h2>Home</h2>;
}

function About() {
    return <h2>About</h2>;
}

function Users() {
    return <h2>Users</h2>;
}
